// Single character
.glyph {
  background: $white;
  margin-top: -80px;
  padding: 15px;
  position: relative;

  .close {
    background: transparent;
    border: 0;
    color: $darker;
    cursor: pointer;
    font-size: 15px;
    height: 40px;
    line-height: 40px;
    position: absolute;
    right: 0;
    text-align: center;
    top: 0;
    width: 40px;

    &:hover,
    &:focus {
      color: $primary;
    }
  }

  h1 {
    color: $darker;
    font-size: 20px;
    font-weight: 600;
    letter-spacing: 0.01em;
    margin: 0 0 10px;
    padding: 0 30px;
    text-align: center;
    text-transform: uppercase;
  }

  h2 {
    color: $black;
    font-family: sans-serif;
    font-size: 8em;
    font-weight: 400;
    line-height: 1.2;
    margin: 0 auto 20px;
    position: relative;
    text-align: center;
    width: 1.25em;
    border: 5px dotted $lighter;

    #glyph {
      -webkit-font-smoothing: antialiased;
      display: block;
      font-family: inherit;
      font-size: inherit;
      line-height: inherit;
      margin: 0 -100%;
      white-space: pre;
    }

    .copy {
      right: -35px;
      top: 0;
    }
  }

  h3 {
    color: $dark;
    font-size: 14px;
    font-weight: 500;
    letter-spacing: 0.01em;
    margin: 0 0 5px;
  }

  pre {
    color: $dark;
    font-size: 18px;
    line-height: 2;
    margin: 0;

    &.js,
    &.html,
    &.css {
      color: $medium;

      span {
        color: $dark;
      }
    }
  }

  @include mq-up($screen-md) {
    margin-top: -120px;
    padding: 30px;

    .close {
      font-size: 20px;
      height: 60px;
      line-height: 60px;
      width: 60px;
    }

    h1 {
      font-size: 30px;
      margin: 0 0 20px;
    }

    h2 {
      margin-bottom: 40px;
    }
  }
}

// Examples
.copy {
  background: 0;
  border: 0;
  cursor: pointer;
  font-size: 14px;
  height: 30px;
  line-height: 30px;
  padding: 0;
  position: absolute;
  right: 0;
  text-align: center;
  top: 8px;
  width: 30px;

  &::after {
    background: transparentize($darker, .15);
    border-radius: 3px;
    color: $white;
    display: block;
    font-size: 12px;
    line-height: 14px;
    margin-right: 4px;
    margin-top: -12px;
    padding: 3px 5px;
    position: absolute;
    right: 100%;
    top: 50%;
    white-space: nowrap;
  }

  &::before {
    border: 4px solid transparent;
    border-left-color: transparentize($darker, .15);
    border-right-width: 0;
    display: block;
    margin-top: -6px;
    position: absolute;
    right: 100%;
    top: 50%;
  }

  &:hover {
    &::after {
      content: 'Copy to clipboard';
    }

    &::before {
      content: '';
    }
  }

  &.copied {
    &::after {
      animation: fade-out 3s cubic-bezier(1, 0, 1, 0);
      content: 'Copied!';
    }

    &::before {
      animation: fade-out 3s cubic-bezier(1, 0, 1, 0);
      content: '';
    }
  }
}

.examples {
  margin-top: 30px;
}

.code,
.example {
  padding-right: 35px;
  position: relative;

  pre {
    background: lighten($lighter, 3%);
    border-radius: 2px;
    border: 1px solid $lighter;
    margin: 0 0 10px;
    padding: 6px 12px;
  }
}
